<template>
  <div>{{ status }}</div>
</template>

<script>
import HomeService from "@/services/HomeService";

export default {
  name: "Home",
  data() {
    return {
      status: "Home Page"
    };
  },
  mounted() {
    this.fetchHome();
  },
  methods: {
    async fetchHome() {
      const response = await HomeService.fetchHome();
      this.status = response.data;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


